  }
  .navbar-btn-group {
    display: none;
  }
  .navbar-burger-menu {
    display: flex;
  }
  .navbar-button4 {
    display: flex;
    padding: var(--dl-space-space-unit);
    flex-direction: row;
  }
  .navbar-logo1 {
